<template>
  <div id="dashboard">
      <h3>Hello</h3>
    <button @click="setData">send data</button>
    <button @click="updateData">update data</button>
  </div>
</template>

<script>
import db from './firebaseinit'
export default {
  name: 'dashboard',
  data () {
    return {
      students: {},
      absents: {
        '17:01': {
          1: {},
          2: {}
        }
      }
    }
  },
  created () {
    db.collection('students').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        console.log(doc.data())
        const data = {
          id: doc.id,
          data: doc.data()
        }
        console.log(data)
      })
    })
  },
  methods: {
    setData () {
      db.collection('students').doc('10:01').set(this.absents)
    },
    updateData () {
      db.collection('students').get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.update({
            1: {
              '11A': ['Vislyakov']
            }
          })
        })
      })
    }
  }
}
</script>

<style scoped>

</style>



